// <script>

export default {
  methods: {
    change (p, q, s, f) {
      this.pagination.total = 22
    },
    refresh () {
      this.source = [{
        key: '',
        name: '',
        age: 25,
        department: '1'
      }, {
        key: '2',
        name: '33',
        age: 33,
        department: '2'
      }, {
        key: '3',
        name: '44',
        age: 12,
        department: '3'
      }, {
        key: '4',
        name: '55',
        age: 25,
        department: '1'
      }, {
        key: '5',
        name: 'l66',
        age: 33,
        department: '2'
      }, {
        key: '61',
        name: 'i6',
        age: 122,
        department: '3'
      }, {
        key: '73',
        name: 'yd',
        age: 2,
        department: '1'
      }, {
        key: '81',
        name: 'b',
        age: 332,
        department: '2'
      }]
    },
    del (key) {
      for (var i in this.source) {
        if (key === this.source[i]['key']) {
          this.source.splice(i, 1)
        }
      }
    }
  },

  data () {
    return {
      selection: {
        checkRows: [],
        onSelect (record, checked, checkedRows) {},
        onSelectAll (checked, checkedRows, changeRows) {},
        getCheckboxProps (record) {
          if (record.key == 2) {
            return {
              disabled: true
            }
          } else {
            return {
              disabled: false
            }
          }
        }
      },
      loading: false,
      pagination: {
        current: 1,
        total: 10,
        pagesize: 10
      },
      filterList: [{
        title: '',
        dataIndex: 'name',
        options: [{value: 'v', label: 'v'}, {value: 't', label: 't'}],
        value: []
      }],
      columns: [{
        title: '',
        dataIndex: 'name',
        sort: true,
        width: '100px',
        filter: true
      }, {
        title: '',
        dataIndex: 'age',
        sort: true,
        sortType: 'DESC',
        sortMethod (x, y) { return x.age - y.age },
        filter: true,
        render: (text, record) => {
          return `<a href="javascript:;">${text}</a>`
        }
      }, {
        title: '',
        dataIndex: 'department'
      }, {
        title: '',
        dataIndex: '',
        render: (text, record, index) => {
          return `<span class="item">
                    <a href="javascript:;" @click="del('${record.key}','${index}')"></a>
                  </span>`
        }
      }],
      source: [{
        key: '1',
        name: '',
        age: 25,
        department: '1'
      }, {
        key: '2',
        name: '',
        age: 33,
        department: '2'
      }, {
        key: '3',
        name: '',
        age: 12,
        department: '3'
      }, {
        key: '4',
        name: 'v',
        age: 25,
        department: '1'
      }, {
        key: '5',
        name: 'l',
        age: 33,
        department: '2'
      }, {
        key: '6',
        name: 'i',
        age: 122,
        department: '3'
      }, {
        key: '7',
        name: 'y',
        age: 2,
        department: '1'
      }, {
        key: '8',
        name: 'b',
        age: 332,
        department: '2'
      }, {
        key: '9',
        name: 't',
        age: 124,
        department: '3'
      }, {
        key: '10',
        name: 'f',
        age: 253,
        department: '1'
      }, {
        key: '11',
        name: 'a',
        age: 31,
        department: '2'
      }, {
        key: '12',
        name: 'd',
        age: 31,
        department: '3'
      }]
    }
  }
}
