// <script>
require('../js/showLanguage')

import prismjs from 'prismjs'
import headerDocs from './headerDocs.vue'
import n3LayoutDocs from './n3LayoutDocs.vue'
import n3IconDocs from './n3IconDocs.vue'
import n3TagsDocs from './n3TagsDocs.vue'
import n3AlertDocs from './n3AlertDocs.vue'
import n3AccordionDocs from './n3AccordionDocs.vue'
import n3CascaderDocs from './n3CascaderDocs.vue'
import n3AffixDocs from './n3AffixDocs.vue'
import n3AsideDocs from './n3AsideDocs.vue'
import n3CarouselDocs from './n3CarouselDocs.vue'
import n3ButtonsDocs from './n3ButtonsDocs.vue'
import n3ToastDocs from './n3ToastDocs.vue'
import n3DatepickerDocs from './n3DatepickerDocs.vue'
import n3TimepickerDocs from './n3TimepickerDocs.vue'
import n3DatetimepickerDocs from './n3DatetimepickerDocs.vue'
import n3DropdownDocs from './n3DropdownDocs.vue'
import n3ModalDocs from './n3ModalDocs.vue'
import n3InputDocs from './n3InputDocs.vue'
import n3InputNumberDocs from './n3InputNumberDocs.vue'
import n3TextareaDocs from './n3TextareaDocs.vue'
import n3NavDocs from './n3NavDocs.vue'
import n3PopoverDocs from './n3PopoverDocs.vue'
import n3ProgressbarDocs from './n3ProgressbarDocs.vue'
import n3SelectDocs from './n3SelectDocs.vue'
import n3SwitchDocs from './n3SwitchDocs.vue'
import n3TimelineDocs from './n3TimelineDocs.vue'
import n3TabsDocs from './n3TabsDocs.vue'
import n3TooltipDocs from './n3TooltipDocs.vue'
import n3TypeaheadDocs from './n3TypeaheadDocs.vue'
import n3StepDocs from './n3StepDocs.vue'
import n3SliderDocs from './n3SliderDocs.vue'
import n3LoadingDocs from './n3LoadingDocs.vue'
import n3SimplePaginationDocs from './n3SimplePaginationDocs.vue'
import n3DataTableDocs from './n3DataTableDocs.vue'
import n3BreadcrumbDocs from './n3BreadcrumbDocs.vue'
import n3MultipleInputDocs from './n3MultipleInputDocs.vue'
import n3TreeDocs from './n3TreeDocs.vue'
import n3UploaderDocs from './n3UploaderDocs.vue'
import n3FormDocs from './n3FormDocs.vue'
import n3CheckboxDocs from './n3CheckboxDocs.vue'
import n3RadioDocs from './n3RadioDocs.vue'
import n3AnimateDocs from './n3AnimateDocs.vue'

export default{
  props: {
    type: {
      type: String
    }
  },
  data () {
    var map, component

    if (this.type === 'base') {
      map = {
        '': {show: true,
                list: [{label: '', value: 'n3LayoutDocs'},
                {label: '', value: 'n3AffixDocs'},
                {label: '', value: 'n3ButtonsDocs'},
                {label: '', value: 'n3IconDocs'}]},

        '': {show: true,
                list: [{label: '', value: 'n3AlertDocs'},
                {label: '', value: 'n3ToastDocs'},
                {label: '', value: 'n3TooltipDocs'},
                {label: '', value: 'n3PopoverDocs'},
                {label: '', value: 'n3LoadingDocs'}]},

        '': {show: true,
                list: [{label: '', value: 'n3InputDocs'},
                {label: '', value: 'n3InputNumberDocs'},
                {label: '', value: 'n3TextareaDocs'},
                {label: '', value: 'n3CascaderDocs'},
                {label: '', value: 'n3RadioDocs'},
                {label: '', value: 'n3CheckboxDocs'},
                {label: '', value: 'n3SelectDocs'},
                {label: '', value: 'n3SliderDocs'},
                {label: '', value: 'n3DatepickerDocs'},
                {label: '', value: 'n3TimepickerDocs'},
                {label: '', value: 'n3DatetimepickerDocs'},
                {label: '', value: 'n3SwitchDocs'},
                {label: '', value: 'n3UploaderDocs'},
                {label: '', value: 'n3TypeaheadDocs'},
                {label: '', value: 'n3MultipleInputDocs'},
                {label: '', value: 'n3FormDocs'}]},

        '': {show: true,
                list: [{label: '', value: 'n3AccordionDocs'},
                 {label: '', value: 'n3AsideDocs'},
                 {label: '', value: 'n3CarouselDocs'},
                 {label: '', value: 'n3DataTableDocs'},
                 {label: '', value: 'n3DropdownDocs'},
                 {label: '', value: 'n3ModalDocs'},
                 {label: '', value: 'n3ProgressbarDocs'},
                 {label: '', value: 'n3TagsDocs'},
                 {label: '', value: 'n3TimelineDocs'},
                 {label: '', value: 'n3TreeDocs'},
                 {label: '', value: 'n3StepDocs'}]},

        '': {show: true,
                list: [{label: '', value: 'n3NavDocs'},
                 {label: '', value: 'n3BreadcrumbDocs'},
                 {label: '', value: 'n3SimplePaginationDocs'},
                 {label: '', value: 'n3TabsDocs'}]},

        '': {show: true,
                list: [{label: '', value: 'n3AnimateDocs'}]}}

      component = 'n3LayoutDocs'
    }

    return {
      component: component,
      anchor: '',
      map: map,
      list: {},
      search: ''
    }
  },
  watch: {
    search (val) {
      let ret = {}
      let map = this.map

      for (let i in map) {
        let show = map[i].show
        let list = map[i].list

        let t = list.filter((i)=>{
          let v = val.toLowerCase()
          let label = i.label
          let value = i.value.toLowerCase().replace('Docs', '').replace('n3', '')

          return label.indexOf(v) > -1 || value.indexOf(v) > -1
        })

        if (t.length) {
          ret[i] = {
            show: show,
            list: t
          }
        }
      }

      this.list = ret

    }
  },
  methods: {
    change (value) {
      this.component = value
      window.location.hash = value
    },
    init () {
      var hash = window.location.hash
      hash = hash.substr(1, hash.length)
      if (hash) {
        this.component = hash
        this.$nextTick(() => {
          prismjs.highlightAll()
        })
      }
    }
  },
  ready () {
    this.list = this.map
    this.init()
    window.onhashchange = this.init
  },
  components: {
    headerDocs,
    n3LayoutDocs,
    n3AlertDocs,
    n3AccordionDocs,
    n3IconDocs,
    n3AffixDocs,
    n3SwitchDocs,
    n3AsideDocs,
    n3ToastDocs,
    n3CarouselDocs,
    n3InputDocs,
    n3TextareaDocs,
    n3ButtonsDocs,
    n3DatepickerDocs,
    n3TimepickerDocs,
    n3DatetimepickerDocs,
    n3InputNumberDocs,
    n3UploaderDocs,
    n3TreeDocs,
    n3DropdownDocs,
    n3ModalDocs,
    n3PopoverDocs,
    n3SelectDocs,
    n3RadioDocs,
    n3CheckboxDocs,
    n3TabsDocs,
    n3TooltipDocs,
    n3TypeaheadDocs,
    n3StepDocs,
    n3NavDocs,
    n3ProgressbarDocs,
    n3TimelineDocs,
    n3LoadingDocs,
    n3SimplePaginationDocs,
    n3SliderDocs,
    n3CascaderDocs,
    n3MultipleInputDocs,
    n3DataTableDocs,
    n3BreadcrumbDocs,
    n3FormDocs,
    n3TagsDocs,
    n3AnimateDocs
  }
}
// </script>


