// <script>
export default {
  data () {
    return {
      query: '',
      items: [],
      a: {list: []}
    }
  },
  methods: {
    render (item) {
      return item.formatted_address
    },
    getResult (query) {
      let self = this
      $.ajax({
        url: 'https://maps.googleapis.com/maps/api/geocode/json?address=' + query,
        success (ret) {
          self.items = ret.results
        }
      })
    },
    del (item, index) {
      this.list.splice(index, 1)
    },
    format (item, index) {
      let content = typeof item === 'string' ? item : item.formatted_address
      return content
    }
  }
}
// </script>




